/****************/
/*** GENERAL ***/
/**************/

/*** OVERRIDES ***/


/*** LOCAL ***/

.empty-div
{
  clear:both;
}

.all-treatments 
{
  background-color: #a5a5a5;
  width: 100vw;
  margin-bottom: 10%;
  padding: 0;
}

.all-treatments h1
{
  color:white;
  font-size: 8vh;
}

.treatments-list 
{
  padding: 0;
  width: 100vw;
}

.treat-elem
{
  color: black;
  display: block;
  margin: 1px 0;
  white-space: pre-wrap;
  padding-top: 0;
  padding-bottom: 0;
  width: 45vw;
  float:left;
  border: 1px solid white;
  background-color: #d6d6d6;
  margin-left: 2.5vw;
  margin-right: 2.5vw;
  margin-bottom: 5vh;
}

.treat-elem h1
{
  font-size: 4vh;
  color: black;
}
.treat-elem p 
{
  font-size: 1.5vh;
}

.social-media-box
{
  background-color: #c6a884;
  height: 20vh;
  width: 100vw;
  /* margin-bottom: 5vh; */
  float: none;
}

.social-media-text 
{
  font-size: 3vh;
  color: #ffffff;
  text-shadow: 0.5px 0.5px black;
  margin-top: 1vh;
  margin-bottom: 2vh;
}
.image-icon 
{
  height: 10vh;
  margin-bottom: 5vh;
} 
.image-icon:hover 
{
  opacity: 0.8;
}

.black-strip {
  background-color: black;
  min-height: 10vh;
  min-width: 100%;
  color: white;
  text-align: center;
  display: table;
  float:left;
}

.black-strip h1 {
  display: table-cell; vertical-align: middle;
  font-size: 4vh;
}

.reviews-list {
  color: white;
  width: 100vw;
}

.review-elem
{
  color: black;
  display: block;
  margin: 1px 0;
  white-space: pre-wrap;
  padding-top: 0;
  padding-bottom: 0;
  width: 29vw;
  float:left;
  border: 1px solid white;
  background-color: #d6d6d6;
  margin-left: 2vw;
  margin-right: 2vw;
  margin-bottom: 5vh;
  min-height: 15vh;
  display: table;
}

.review-elem p 
{
  display: table-cell;
  vertical-align: middle;
  font-size: 2vh;
}

.carousel-container img {
  margin: 0 auto;
}

