{
    /*** OVERRIDES ***/


/*** LOCAL ***/
.social-media-box
{
  float:left;

}

.treatments-text
{
  padding-top: 0;
  margin-top: 0;
}

.treat-elem
{
  margin-bottom: 10%;
  width: 100vw;
}
.treat-elem h1
{
  font-size: 3vh;
}

.treat-elem p 
{
  padding: 0vh 4.5vw;
}
.review-elem {
  margin-bottom: 10%;
  width: 100vw;
}
.review-elem p {
  padding: 0vh 4.5vw;
}

.black-strip h1 {
  font-size: 3vh;
}
}