/****************/
/*** GENERAL ***/
/**************/

/*** OVERRIDES ***/
.navbar-inverse
{
  background-color: #010203;
}

.fa-arrow-circle-down
{
    padding-top:30vh;
    font-size: 10vh;
    color:white;
}

/*** LOCAL ***/
.head_container
{
  background: url('./images/oils2-opacity.png');
  background-attachment: fixed;
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: 25%;
  color:white;
  height: 100vh;
  width: 100vw;
  text-align: center;
}


.title_text
{
  padding-top: 20vh;
  text-align: center;
  font-size: 15vh;
  text-shadow: 1px 1px #010203;
  font-weight: bold;
}

.desc_text
{
  font-size: 4vh;
  padding-top: 5vh;
  padding-left: 10vw;
  padding-right: 10vw;
  text-shadow: 1px 1px #010203;
}



/**********************/
/*** MEDIA QUERIES ***/
/********************/


/*** UP TO TABLETS ***/
@media only screen and (max-width: 1024px)
{
    /*** OVERRIDES ***/
  
    /*** LOCAL ***/
  .title_text{
    font-size: 10vh;
  }}

/*** UP TO PHONES ***/
@media only screen and (max-width: 768px)
{

    /*** OVERRIDES ***/
    .fa-arrow-circle-down
    {
        visibility: none;
        display: none;
    }
  
    /*** LOCAL ***/
  .hide_on_mobile
  {
    visibility: hidden;
    display: none;
  }
  .head_container {
    background-position-y: 0%; 
    height: 75vh;
  }

  .title_text{
    font-size: 8vh;
    padding-top: 12.5vh;
    font-weight: normal;
  }
  .desc_text {
    font-size: 3vh;
    padding-top: 7.5vh;
    }
}