body {
  margin: 0;
  padding: 0;
  font-family: "GFS Didot", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "GFS Didot", serif;
}

/****************/
/*** GENERAL ***/
/**************/

/*** OVERRIDES ***/
.navbar-inverse
{
  background-color: #010203;
}

.fa-arrow-circle-down
{
    padding-top:30vh;
    font-size: 10vh;
    color:white;
}

/*** LOCAL ***/
.head_container
{
  background: url(../../static/media/oils2-opacity.0f304f81.png);
  background-attachment: fixed;
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: 25%;
  color:white;
  height: 100vh;
  width: 100vw;
  text-align: center;
}


.title_text
{
  padding-top: 20vh;
  text-align: center;
  font-size: 15vh;
  text-shadow: 1px 1px #010203;
  font-weight: bold;
}

.desc_text
{
  font-size: 4vh;
  padding-top: 5vh;
  padding-left: 10vw;
  padding-right: 10vw;
  text-shadow: 1px 1px #010203;
}



/**********************/
/*** MEDIA QUERIES ***/
/********************/


/*** UP TO TABLETS ***/
@media only screen and (max-width: 1024px)
{
    /*** OVERRIDES ***/
  
    /*** LOCAL ***/
  .title_text{
    font-size: 10vh;
  }}

/*** UP TO PHONES ***/
@media only screen and (max-width: 768px)
{

    /*** OVERRIDES ***/
    .fa-arrow-circle-down
    {
        visibility: none;
        display: none;
    }
  
    /*** LOCAL ***/
  .hide_on_mobile
  {
    visibility: hidden;
    display: none;
  }
  .head_container {
    background-position-y: 0%; 
    height: 75vh;
  }

  .title_text{
    font-size: 8vh;
    padding-top: 12.5vh;
    font-weight: normal;
  }
  .desc_text {
    font-size: 3vh;
    padding-top: 7.5vh;
    }
}
/****************/
/*** GENERAL ***/
/**************/

/*** OVERRIDES ***/


/*** LOCAL ***/

.empty-div
{
  clear:both;
}

.all-treatments 
{
  background-color: #a5a5a5;
  width: 100vw;
  margin-bottom: 10%;
  padding: 0;
}

.all-treatments h1
{
  color:white;
  font-size: 8vh;
}

.treatments-list 
{
  padding: 0;
  width: 100vw;
}

.treat-elem
{
  color: black;
  display: block;
  margin: 1px 0;
  white-space: pre-wrap;
  padding-top: 0;
  padding-bottom: 0;
  width: 45vw;
  float:left;
  border: 1px solid white;
  background-color: #d6d6d6;
  margin-left: 2.5vw;
  margin-right: 2.5vw;
  margin-bottom: 5vh;
}

.treat-elem h1
{
  font-size: 4vh;
  color: black;
}
.treat-elem p 
{
  font-size: 1.5vh;
}

.social-media-box
{
  background-color: #c6a884;
  height: 20vh;
  width: 100vw;
  /* margin-bottom: 5vh; */
  float: none;
}

.social-media-text 
{
  font-size: 3vh;
  color: #ffffff;
  text-shadow: 0.5px 0.5px black;
  margin-top: 1vh;
  margin-bottom: 2vh;
}
.image-icon 
{
  height: 10vh;
  margin-bottom: 5vh;
} 
.image-icon:hover 
{
  opacity: 0.8;
}

.black-strip {
  background-color: black;
  min-height: 10vh;
  min-width: 100%;
  color: white;
  text-align: center;
  display: table;
  float:left;
}

.black-strip h1 {
  display: table-cell; vertical-align: middle;
  font-size: 4vh;
}

.reviews-list {
  color: white;
  width: 100vw;
}

.review-elem
{
  color: black;
  display: block;
  margin: 1px 0;
  white-space: pre-wrap;
  padding-top: 0;
  padding-bottom: 0;
  width: 29vw;
  float:left;
  border: 1px solid white;
  background-color: #d6d6d6;
  margin-left: 2vw;
  margin-right: 2vw;
  margin-bottom: 5vh;
  min-height: 15vh;
  display: table;
}

.review-elem p 
{
  display: table-cell;
  vertical-align: middle;
  font-size: 2vh;
}

.carousel-container img {
  margin: 0 auto;
}

/**********************/
/*** MEDIA QUERIES ***/
/********************/

/*** UP TO TABLETS ***/
/*** OVERRIDES ***/
/*** LOCAL ***/

/*** UP TO PHONES ***/
@media only screen and (max-width: 767px)
{
    /*** OVERRIDES ***/


/*** LOCAL ***/
.social-media-box
{
  float:left;

}

.treatments-text
{
  padding-top: 0;
  margin-top: 0;
}

.treat-elem
{
  margin-bottom: 10%;
  width: 100vw;
}
.treat-elem h1
{
  font-size: 3vh;
}

.treat-elem p 
{
  padding: 0vh 4.5vw;
}
.review-elem {
  margin-bottom: 10%;
  width: 100vw;
}
.review-elem p {
  padding: 0vh 4.5vw;
}

.black-strip h1 {
  font-size: 3vh;
}
}
/****************/
/*** GENERAL ***/
/**************/

/*** OVERRIDES ***/

/*** LOCAL ***/

.empty-div {
  clear:both;
}

.main_footer
{
    width: 100vw;
    background-color: #d6d6d6;
    text-align: center;
    margin-top: 5vh;
    padding-bottom: 2vh;
    font-family: 'Roboto', 'Georgia';
}

.main-footer h1 {
  margin-top: 5vh;

}

.map
{
  border: 0;
  width: 40vw;
  height: 50vh;
}

.footer_text
{
  padding-top: 3vh;
  padding-bottom: 1vh;
  font-size: 5vh;
  font-weight: bold;
  color: white;
  margin-text: 5vh;
  text-shadow: 1px 1px black;
}

.footer_desc
{
  font-size: 2vh;
  padding-left: 25vw;
  padding-right: 25vw;
  padding-bottom: 2vh;
} 

.address
{
  position: relative;
  left: -15vw;
  font-size: 2.5vh;
}

.footer_box
{
    width:100%;
    height:2vh;
    color: #CDD0D7;
    background-color: #010203;
    text-align: center;
    font-size: 1.2vh;
    font-family: 'Roboto','Georgia'; 
}

/**********************/
/*** MEDIA QUERIES ***/
/********************/

@media only screen and (max-width: 1200px)
{
    /*** OVERRIDES ***/


/*** LOCAL ***/
  .footer_map
  {
    height: 50vh;
    width: 50vw;
  }
  .footer_desc
  {
    padding-left: 10vw;
    padding-right: 10vw;
  }
  .address
  {
    left: 0;
    font-size: 2vh;
  }
  .address h4
  {
    left: 0;
    font-size: 1.5vh;
  }
  .map
  {
    height: 40vh;
    width: 90vw;
    margin-right: auto;
    margin-left: auto;
  }

}

/*** UP TO PHONES ***/
@media only screen and (max-width: 767px)
{
    /*** OVERRIDES ***/


/*** LOCAL ***/
    .footer_text
    {
      font-weight: normal;
    }
    .map
    {
      height: 40vh;
      width: 90vw;
    }
    .footer_desc
    {
        padding-left: 7.5vw;
        padding-right: 7.5vw;
    }
}
.main_container
{
  margin: 0;
  padding: 0;
  height:100%;
  width:100vw;
  background-color:  #a5a5a5;
  overflow-x: hidden;
  font-family: 'GFS Didot', serif;
}
