/****************/
/*** GENERAL ***/
/**************/

/*** OVERRIDES ***/

/*** LOCAL ***/

.empty-div {
  clear:both;
}

.main_footer
{
    width: 100vw;
    background-color: #d6d6d6;
    text-align: center;
    margin-top: 5vh;
    padding-bottom: 2vh;
    font-family: 'Roboto', 'Georgia';
}

.main-footer h1 {
  margin-top: 5vh;

}

.map
{
  border: 0;
  width: 40vw;
  height: 50vh;
}

.footer_text
{
  padding-top: 3vh;
  padding-bottom: 1vh;
  font-size: 5vh;
  font-weight: bold;
  color: white;
  margin-text: 5vh;
  text-shadow: 1px 1px black;
}

.footer_desc
{
  font-size: 2vh;
  padding-left: 25vw;
  padding-right: 25vw;
  padding-bottom: 2vh;
} 

.address
{
  position: relative;
  left: -15vw;
  font-size: 2.5vh;
}

.footer_box
{
    width:100%;
    height:2vh;
    color: #CDD0D7;
    background-color: #010203;
    text-align: center;
    font-size: 1.2vh;
    font-family: 'Roboto','Georgia'; 
}

/**********************/
/*** MEDIA QUERIES ***/
/********************/

@media only screen and (max-width: 1200px)
{
    /*** OVERRIDES ***/


/*** LOCAL ***/
  .footer_map
  {
    height: 50vh;
    width: 50vw;
  }
  .footer_desc
  {
    padding-left: 10vw;
    padding-right: 10vw;
  }
  .address
  {
    left: 0;
    font-size: 2vh;
  }
  .address h4
  {
    left: 0;
    font-size: 1.5vh;
  }
  .map
  {
    height: 40vh;
    width: 90vw;
    margin-right: auto;
    margin-left: auto;
  }

}

/*** UP TO PHONES ***/
@media only screen and (max-width: 767px)
{
    /*** OVERRIDES ***/


/*** LOCAL ***/
    .footer_text
    {
      font-weight: normal;
    }
    .map
    {
      height: 40vh;
      width: 90vw;
    }
    .footer_desc
    {
        padding-left: 7.5vw;
        padding-right: 7.5vw;
    }
}